.ingredients {
  width: 50%;
}

.scroll {
  max-height: calc(100vh - (100vh) / 2.4);
  overflow-y: scroll;
  overflow-x: hidden;
}

.tabs {
  display: flex;
}

.list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.info {
  position: relative;
  display: flex;
  margin-bottom: 30px;
  margin-right: 50px;
  align-items: center;
  flex-direction: column;
  width: calc(100% / 2.5);
  cursor: pointer;
}

.name {
  text-align: center;
}

.price {
  display: flex;
  align-items: center;
}

.counter {
  position: relative;
  left: 100px;
}